<template>
    <div class="row">
    <!--right-->
    <div data-app class="col-12">                   
            <div class="form-group">
            <!--live search-->           
                <div class='col-12' style="display:flex;flex-direction:row;padding-left:0;padding-right:0">
                    <div class='col-4'>
                        <label>Select Department</label>
                        <select v-model="dept" class="form-control" v-if="dept_ready" @change="new_list">
                            <option v-for="dep in dept_list" :value="dep.deptmentid">{{ dep.department }}</option>
                        </select>                                      
                    </div>
                    <!--
                    <div class='col-2' style="display:flex;flex-direction:row;margin-left:auto;">
                        <div>
                            <label style=";width:7em">Fund Type</label>
                            <select class="form-control" v-model="fund_current" @change="new_list">
                                <option v-for="type in fund_type ">{{ type }}</option>
                            </select>                                      
                        </div>
                    </div>-->
                    <div class="col-2">
                        <label>&nbsp;</label>
                        <button class="form-control" @click="carry()">Carry fundings</button>
                    </div>
                    <div class='col-6' style="margin-left:auto;display:flex;flex-direction:row;padding-left:0;padding-right:0">                                           
                        <div class='col-6'>
                            <!--
                            <label>Start period</label>
                            <input type="month" v-model="st" class="form-control"  @change="new_list"/>
                            -->
                        </div>
                        <div class='col-6'>
                            <label>School Year</label>
                            <div class="col-12 flex px0">
                                <div class="col-6 pl0">
                                    <input type="number" v-model="st" class="form-control"  @change="update_period"/>
                                </div>
                                <div class="col-6 pr0">
                                    <input type="number" v-model="ed" class="form-control" readonly/>
                                </div>
                            </div>                           
                        </div>
                    </div>
                </div>
            </div>           
            <div class='col-12'>
                <v-data-table
                :headers="headers"
                :items="fundlist"
                class="elevation-1"
                :rows-per-page-items="[10]"
                >
                <template v-slot:items="props">
                    <td><a href="" :id= props.item.id v-on:click.prevent="remove($event)">Remove</a></td>
                    <td>{{ props.item.name }}</td>
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>        
                </template>
                </v-data-table><br/>
            <!--member-->
            
            <div class="form-group">
            <!--live search-->           
                <div class="col-12 px0 flex">
                    <div class="col-4 px0">
                        <input type='text' class='form-control' maxlength="35" v-model="insert_fund_name" placeholder="Insert New Fund"/> 
                    </div>
                    <div class="col-5"></div>
                    <div class="col-3 px0">
                        <div class="col-12 pr0">
                            <div class="col-12 pr0">
                                <input type='button' class='form-control' value="Insert" @click="new_fund_insert"/>
                            </div>
                        </div>
                    </div>                                    
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
             headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Fund',value:'name',sortable: false},
            ], 
            dept_list:[],
            dept:'',
            dept_ready:false,
            fundlist:[],
            st:'',
            ed:'',
            insert_fund_name:'',
            fund_current : 'Regular',
            fund_type:['Regular','Special'],
            user_check:'',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<3){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member_head.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                var query_dpt = document.getElementById('query').value;
                if((query_dpt!=undefined)&&(query_dpt!='')){
                    this.dept = query_dpt;
                } else {
                    this.dept = this.dept_list[0].deptmentid;
                }
                
                this.dept_ready = true;
            }
        );   
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )   
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('type',this.fund_current);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    await axios.post('Procurement/department/backend/fundlist.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;
            }
        )    
},
methods:{
    async update_period(){
        this.ed = parseInt(this.st)+1;
        this.$forceUpdate();
        await this.new_list();
    },
    carry(){
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('type',this.fund_current);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/department/backend/carry_fund.php',formData)
            .then(
                (response)=>{
                    if(response.data=="OK"){
                        this.$alert("Successfully carried funding sources to "+parseInt(this.st)+1+' - '+parseInt(this.ed)+1)
                            .then(
                                ()=>{
                                    this.new_list();
                                }
                            )                   
                    }
                }
            )
    },
    new_list(event){
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('type',this.fund_current);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    axios.post('Procurement/department/backend/fundlist.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;               
            }
        )
    },
    new_fund_insert(event){
        var formData = new FormData();
        formData.append('fund',this.insert_fund_name);
        formData.append('dept',this.dept);
        formData.append('type',this.fund_current);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/department/backend/new_fund.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;
            }
        );
        this.insert_fund_name = '';
    },
    remove(){
        var targetID = event.currentTarget.id;
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('type',this.fund_current);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        formData.append('id',targetID);
        axios.post('Procurement/department/backend/remove_fund.php',formData)
        .then(
            (response)=>{
                this.fundlist = response.data;
            }
        )
    }
}
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction: row;
}
.px0{
    padding-left:0;
    padding-right: 0;
}
.pl0{
    padding-left:0;
}
.pr0{
    padding-right:0;
}
</style>


